import React from 'react';
import {
  connect
} from 'react-redux';
import NavBar from '../../components/NavBar';
import * as AccountActions from './action'
import {
  bindActionCreators
} from 'redux';
import Article from './../../components/Article'
class Detail extends React.Component { // eslint-disable-line
  componentWillMount() {
    console.log(`componentWillMount`)
    const param = {
      classid:this.props.routeParams.classid,
      id:this.props.routeParams.id,
    };
    this.props.getNewsContent(param)
  };
  render() {
    return (
      <div>
      <NavBar param={this.props} />
      <Article param={this.props}/>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const stateJS = state.toJS();
  var result = {};
  // 将state中各模块的数据中取出合并
  for (var i in stateJS) {
    for (var j in stateJS[i]) {
      result[j] = stateJS[i][j]
    }
  }
  return (result);
}


function mapDispatchToProps(dispatch) {
  // load headline
  return bindActionCreators({...AccountActions
  }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Detail);


