/*
** 轮播图效果
*/

import React from 'react';
import styled from 'styled-components';
const DetailWrappp = styled.div`
	width:640px;
	margin:30px auto;
`;
class Article extends React.Component{
	render(){
		const {content} = this.props.param;
		console.log(content)
		return (
			<DetailWrappp>
				<h1>{content.title}</h1>
				<div className="source">
					<span className="time">时间：{content.newstime}</span>
					<span className="keyword">关键词：{content.keyboard}</span>
				</div>
				<div class="description">{content.smalltext}</div>
				<div className="content" dangerouslySetInnerHTML={{__html: content.newstext}}></div>
			</DetailWrappp>
			)
	}
	
}


export default Article;


